
import './App.css';
import "bootstrap/dist/css/bootstrap.min.css";
import {Particles} from 'react-particles-js';
import {Navbar} from './components/Navbar';
import {Header} from "./components/Header";

function App() {
  return (
    <>
    <Particles
      className="particles-canvas"
      params= {{
        particles: {

          number:{
            value: 50,
            density:{
              enable: true,
              value_area: 900
            }
          },
          line_linked: {
            shadow: {
              enable: true,
              color: "#000",
              blur: 1
            }
          }, 
          color: {
            value: "#000"
          },
          shape:{
            type: "circle",
            stroke:{
              width: 6,
              color: "#000"
            }
          }
        }

      }} />

      <Navbar />
      <Header />


    </>
  );
}

export default App;
